"use client";
import React, { useState, useRef, useEffect } from "react";
import { useRouter } from "next/navigation";
import { FaSearch, FaBook, FaGamepad, FaTimes } from "react-icons/fa";

export default function SearchBar() {
  const [searchQuery, setSearchQuery] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const [filteredResults, setFilteredResults] = useState([]);
  const searchRef = useRef(null);
  const router = useRouter();

  // Sample data for reviewers and quizzes
  const searchData = [
    // Reviewers
    { id: 1, type: "reviewer", title: "Introduction to Computer Science", subject: "CS101", icon: FaBook, link: "/student/reviewers/1" },
    { id: 2, type: "reviewer", title: "Data Structures and Algorithms", subject: "CS102", icon: FaBook, link: "/student/reviewers/2" },
    { id: 3, type: "reviewer", title: "Database Management Systems", subject: "CS201", icon: FaBook, link: "/student/reviewers/3" },
    { id: 4, type: "reviewer", title: "Web Development Fundamentals", subject: "CS202", icon: FaBook, link: "/student/reviewers/4" },
    { id: 5, type: "reviewer", title: "Object-Oriented Programming", subject: "CS103", icon: FaBook, link: "/student/reviewers/5" },
    { id: 6, type: "reviewer", title: "Operating Systems", subject: "CS301", icon: FaBook, link: "/student/reviewers/6" },
    { id: 7, type: "reviewer", title: "Computer Networks", subject: "CS302", icon: FaBook, link: "/student/reviewers/7" },
    { id: 8, type: "reviewer", title: "Software Engineering", subject: "CS303", icon: FaBook, link: "/student/reviewers/8" },
    
    // Quizzes
    { id: 9, type: "quiz", title: "CS101 Midterm Quiz", subject: "CS101", icon: FaGamepad, link: "/student/quizzes/1" },
    { id: 10, type: "quiz", title: "Data Structures Final Quiz", subject: "CS102", icon: FaGamepad, link: "/student/quizzes/2" },
    { id: 11, type: "quiz", title: "Database Basics Quiz", subject: "CS201", icon: FaGamepad, link: "/student/quizzes/3" },
    { id: 12, type: "quiz", title: "HTML & CSS Quiz", subject: "CS202", icon: FaGamepad, link: "/student/quizzes/4" },
    { id: 13, type: "quiz", title: "OOP Concepts Quiz", subject: "CS103", icon: FaGamepad, link: "/student/quizzes/5" },
    { id: 14, type: "quiz", title: "Operating Systems Quiz", subject: "CS301", icon: FaGamepad, link: "/student/quizzes/6" },
    { id: 15, type: "quiz", title: "Networking Fundamentals Quiz", subject: "CS302", icon: FaGamepad, link: "/student/quizzes/7" },
  ];

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (searchRef.current && !searchRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  // Filter results based on search query
  useEffect(() => {
    if (searchQuery.trim().length > 0) {
      const query = searchQuery.toLowerCase();
      const results = searchData.filter(
        (item) =>
          item.title.toLowerCase().includes(query) ||
          item.subject.toLowerCase().includes(query) ||
          item.type.toLowerCase().includes(query)
      );
      setFilteredResults(results);
      setIsOpen(true);
    } else {
      setFilteredResults([]);
      setIsOpen(false);
    }
  }, [searchQuery]);

  const handleInputChange = (e) => {
    setSearchQuery(e.target.value);
  };

  const handleResultClick = (item) => {
    setSearchQuery("");
    setIsOpen(false);
    router.push(item.link);
  };

  const handleClearSearch = () => {
    setSearchQuery("");
    setIsOpen(false);
  };

  const groupedResults = {
    reviewers: filteredResults.filter((item) => item.type === "reviewer"),
    quizzes: filteredResults.filter((item) => item.type === "quiz"),
  };

  return (
    <div className="search-bar-container" ref={searchRef}>
      <div className="search-bar">
        <FaSearch className="search-icon" />
        <input
          type="text"
          placeholder="Search for reviewers, quizzes..."
          value={searchQuery}
          onChange={handleInputChange}
          onFocus={() => searchQuery && setIsOpen(true)}
        />
        {searchQuery && (
          <button className="clear-search-btn" onClick={handleClearSearch}>
            <FaTimes />
          </button>
        )}
      </div>

      {isOpen && filteredResults.length > 0 && (
        <div className="search-dropdown">
          {groupedResults.reviewers.length > 0 && (
            <div className="search-group">
              <div className="search-group-title">Reviewers</div>
              {groupedResults.reviewers.map((item) => {
                const IconComponent = item.icon;
                return (
                  <div
                    key={item.id}
                    className="search-result-item"
                    onClick={() => handleResultClick(item)}
                  >
                    <div className="search-result-icon">
                      <IconComponent />
                    </div>
                    <div className="search-result-content">
                      <div className="search-result-title">{item.title}</div>
                      <div className="search-result-subject">{item.subject}</div>
                    </div>
                  </div>
                );
              })}
            </div>
          )}

          {groupedResults.quizzes.length > 0 && (
            <div className="search-group">
              <div className="search-group-title">Quizzes</div>
              {groupedResults.quizzes.map((item) => {
                const IconComponent = item.icon;
                return (
                  <div
                    key={item.id}
                    className="search-result-item"
                    onClick={() => handleResultClick(item)}
                  >
                    <div className="search-result-icon">
                      <IconComponent />
                    </div>
                    <div className="search-result-content">
                      <div className="search-result-title">{item.title}</div>
                      <div className="search-result-subject">{item.subject}</div>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>
      )}

      {isOpen && searchQuery && filteredResults.length === 0 && (
        <div className="search-dropdown">
          <div className="no-search-results">
            <FaSearch className="empty-search-icon" />
            <p>No results found for "{searchQuery}"</p>
          </div>
        </div>
      )}
    </div>
  );
}
